//import { ADD_TODO } from '../actions';
import { CHANGE_ACTIVE_PDF } from '../actions';
import { CHANGE_TEXT_VALUE } from '../actions';
import { REMOVE_TEXT_VALUE } from '../actions';
import { ADD_NEW_TEXT_VALUE } from '../actions';
import { REMOVE_MESSAGE } from '../actions';
import { ADD_NEW_MESSAGE } from '../actions';
import { ADD_FIRST_MESSAGE } from '../actions';
import { REMOVE_PAGE } from '../actions';
import { ADD_NEW_PAGE } from '../actions';
import { SIGN_FORM_AND_ADD_NEW_PAGE } from '../actions';
import { SIGN_FORM } from '../actions';
import { SIGN_OUT_FORM } from '../actions';
import { ADD_PDF } from '../actions';
import { REMOVE_PDF } from '../actions';

const initialState = {
  //toDoList: [],
  
	current_user: {
		id: 0
	},
	users: [{
		id: 0,
		name: "Olka",
		pass: "123"
	},
	{
		id: 1,
		name: "Barabolka",
		pass: "123"
	}],
	pdfs: [{
		path: "some path1",
		id: 0,
		user_id: 0,
		images: ["/uploads/photos/photo_pdf билета.png", "../photos/2.jpg", "../photos/3.jpg", "../photos/4.jpg", "../photos/5.jpg", "../photos/6.jpg", "../photos7.jpg", "../photos/8.jpg"]
	},{
		path: "some path2",
		id: 1,
		user_id: 0,
		images: ["../photos/2.jpg"]
	}],
	texts: [{
		text: "testing text1",
		x: 0,
		y:0,
		page_id: 0,
		image_index: 0,
		id: 0,
		max_width: "auto",
		status: "ok" //"ok", "warning"
	},
	{
		text: "testing text2",
		x: 100,
		y:100,
		page_id: 0,
		image_index: 0,
		id: 1,
		max_width: "auto",
		status: null
	}	
	],
	messages: [{
		id: 0,
		user_id: null,		
		text_id: 0,
		text: "message1",
		date: "date1"
	},
	{
		id: 1,
		user_id: 0,		
		text_id: 0,
		text: "message2",
		date: "date2"
	}],
	active_pdf_index: 0,
	pages: [{
		name: "page 1",
		id: 0,
		pdf_id: 0,
		user_id: 0
	},{
		name: "page 2",
		id: 1,
		pdf_id: 1,
		user_id: 0
	},{
		name: "page 3",
		id: 2,
		pdf_id: 0,
		user_id: 0
	}]  
};

export default function toDoApp(state = initialState, action) {
	let newTexts, newMessages, 
		id, newText, newMessage, 
		newPages, newPage, cUserId,
		newUsers, fUsers, pageId;
	
	switch (action.type) {
	case CHANGE_ACTIVE_PDF:
		return {
			...state,
			active_pdf_index: action.index
		}
	case CHANGE_TEXT_VALUE: 	
		newTexts = state.texts.map((txt)=>{			
			if(txt.id == action.id){
				txt.text = action.text;
				txt.max_width = action.max_width;	
				txt.x = action.x;	
				txt.y = action.y;
				txt.status = action.status;
			}
			return txt;
		});
		
		return {
			...state,
			texts: newTexts
		}
	case ADD_NEW_TEXT_VALUE: 			
		newText = {
			text: action.text,
			x: action.x,
			y:action.y,
			page_id: action.page_id,
			image_index: action.image_index,
			id: action.id,
			max_width: action.max_width,
			status: null
		};
		return {
			...state,
			texts: [...state.texts, newText]
		}	
	case ADD_NEW_MESSAGE: 
		id = state.messages.length > 0 ? state.messages[state.messages.length-1].id + 1 : 0;
		newMessage = {
			id: id,
			user_id: action.user_id,
			text_id: action.text_id,
			text: action.text,
			date: action.date				
		};		
		return {
			...state,
			messages: [
				...state.messages,
				newMessage
			]
		}
	case ADD_FIRST_MESSAGE: 
		newText = {
			text: action.text,
			x: action.x,
			y:action.y,
			page_id: action.page_id,
			image_index: action.image_index,
			id: action.id,
			max_width: action.max_width,
			status: null
		};	
		id = state.messages.length > 0 ? state.messages[state.messages.length-1].id + 1 : 0;
		newMessage = {
			id: id,
			user_id: action.user_id,
			text_id: action.id,
			text: action.msgText,
			date: action.date			
		};
		
		return {
			...state,
			texts: [...state.texts, newText],
			messages: [
				...state.messages,
				newMessage
			]
		}	
	case ADD_NEW_PAGE:
		id = state.pages.length > 0 ? state.pages[state.pages.length-1].id + 1 : 0;
		newPage = {
			name: action.name,
			id: id,
			pdf_id: action.pdf_id,
			user_id: action.user_id
		};
		return {
			...state,
			pages: [
				...state.pages,
				newPage
			]
		}			
	case REMOVE_TEXT_VALUE: 	
		newTexts = state.texts.filter((txt)=>{			
			return txt.id !== action.id;	
		});	
		newMessages = state.messages.filter((msg)=>{			
			return msg.text_id !== action.id;	
		});		
		return {
			...state,
			texts: newTexts,
			messages: newMessages
		}	
	case REMOVE_MESSAGE: 	
		newMessages = state.messages.filter((msg)=>{			
			return msg.id !== action.id;	
		});		
		
		return {
			...state,
			messages: newMessages
		}	
	case REMOVE_PAGE: 	
		newPages = state.pages.filter((pg)=>{
			return pg.id !== action.id;
		});
		newTexts = state.texts.filter((txt)=>{			
			return txt.page_id !== action.id;	
		});	
		let oldTexts = state.texts.filter((txt)=>{			
			return txt.page_id == action.id;	
		});	
		let newTextsIds = oldTexts.map((txt)=>{
			return txt.id;
		});
		newMessages = state.messages.filter((msg)=>{			
			return newTextsIds.indexOf(msg.text_id) == -1;	
		});			
		
		return {
			...state,
			pages: newPages,
			texts: newTexts,
			messages: newMessages
		}	
	case SIGN_FORM:
		cUserId = null;
		newUsers = [...state.users];
		fUsers = state.users.filter((us)=>{
			return us.name == action.name && us.pass == action.pass;
		});
		if(fUsers.length > 0){
			cUserId = fUsers[0].id;
		}
		else{
			id = state.users.length > 0 ? state.users[state.users.length-1].id + 1 : 0;
			newUsers = [
				...state.users,
				{
					id: id,
					name: action.name,
					pass: action.pass
				}
			]
			cUserId = id;
		}
		return {
			...state,
			current_user: {
				id: cUserId
			},
			users: [...newUsers]
		}
	case SIGN_OUT_FORM:
		return {
			...state,
			current_user: {
				id: null
			}
		}
	case SIGN_FORM_AND_ADD_NEW_PAGE:
		cUserId = null;
		newUsers = [...state.users];
		fUsers = state.users.filter((us)=>{
			return us.name == action.login && us.pass == action.pass;
		});
		if(fUsers.length > 0){
			cUserId = fUsers[0].id;
		}
		else{
			id = state.users.length > 0 ? state.users[state.users.length-1].id + 1 : 0;
			newUsers = [
				...state.users,
				{
					id: id,
					name: action.login,
					pass: action.pass
				}
			]
			cUserId = id;
		}	
		pageId = state.pages.length > 0 ? state.pages[state.pages.length-1].id + 1 : 0;
		newPage = {
			name: action.name,
			id: pageId,
			pdf_id: action.pdf_id,
			user_id: cUserId
		};
		return {
			...state,
			pages: [
				...state.pages,
				newPage
			],
			current_user: {
				id: cUserId
			},
			users: [...newUsers]			
		}
	case ADD_PDF:
		id = state.pdfs.length > 0 ? state.pdfs[state.pdfs.length-1].id + 1 : 0;
		let newPdf = {
			id: id,
			path: action.path,
			images: action.images,
			user_id: action.user_id
		};
		return {
			...state,
			pdfs: [
				...state.pdfs,
				newPdf
			]			
		}
	case REMOVE_PDF:
		
		return {
			...state
		}
    default:
      return state;
  }
}