import React from 'react';
import { connect } from 'react-redux';
import ListGroup from 'react-bootstrap/ListGroup';
import Jumbotron from 'react-bootstrap/Jumbotron';
import Button from 'react-bootstrap/Button';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus } from '@fortawesome/free-solid-svg-icons';

import { addNewPage, signFormAndAddNewPage} from '../actions';

import UserModal from './UserModal';
import PageView from './PageView';


const mapStateToProps = state => {
	
	return {	
		
		pages: state.pages,
		pdfs: state.pdfs,
		active_pdf_index: state.active_pdf_index,
		users: state.users,
		current_user: state.current_user		
	};
};


let PageBlock = connect(mapStateToProps)(({ pages, pdfs, active_pdf_index, users, current_user, dispatch }) => {
	let activePdfId = pdfs.length > 0 ? pdfs[active_pdf_index].id : null;
	let activePages = pages.filter((item)=>{
		return activePdfId == item.pdf_id;
	});	
	let handleAddNewPage = ()=>{
		
	}
	let addPageObj = {
		signIn: false,
		signUp: false,
		addPage: true,
		title: "Add New Page",
		"button": {
			title: "Add New Page" ,
			icon: faPlus
		},
		buttons: [{
			title: "Cancel"			
		},{
			title: "Add Page",
			func: function(fieldsValue){
				dispatch(addNewPage({
					name: fieldsValue.name,
					pdf_id: activePdfId,
					user_id: current_user.id
				}));
			}
		}]		
	}
	let signModalObj = {
		signIn: true,
		signUp: true,
		addPage: true,
		title: "Add New Page",
		"button": {
			title: "Add New Page" ,
			icon: faPlus
		},
		buttons: [{
			title: "Cancel"			
		},{
			title: "Ok",
			func: function(fieldsValue){
				dispatch(signFormAndAddNewPage({
					name: fieldsValue.name,
					pdf_id: activePdfId,
					
					login: fieldsValue.login,
					pass: fieldsValue.pass
				}));				
				console.log(fieldsValue, "fieldsValue");
				console.log("is ok", pages);
			}
		}]
	};

	return(
	<ListGroup className="list-group-flush">
		{activePages.map((page, index) => {	
			let pageUsers = users.filter((us)=>{
				return us.id == page.user_id;
			});
			let user = pageUsers.length > 0 ? pageUsers[0] : null;		
			let activePage = {
				...page,
				user: user,
				current_user: current_user
			}
			return(			
				<PageView key={index} {...{page: activePage}} />
			)}
		)}		
		<ListGroup.Item>
		{current_user.id !== null ? <UserModal {...addPageObj} />	 :	<UserModal {...signModalObj} />	}
			
		</ListGroup.Item>
	</ListGroup>
)});

export default PageBlock;