import React from 'react';
import { connect } from 'react-redux';
import ListGroup from 'react-bootstrap/ListGroup';
import Button from 'react-bootstrap/Button';
import ButtonGroup from 'react-bootstrap/ButtonGroup';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus, faTrashAlt, faChevronRight } from '@fortawesome/free-solid-svg-icons';

import { changeActivePdf, removePdf } from '../actions';

import UserModal from './UserModal';

let PdfView = ({pdf, dispatch}) => {	
	let signModalRemoveObj = {
		signIn: false,
		signUp: false,
		sureRemovePdf: true,
		title: "Remove Pdf File",		
		"button": {
			title: "Remove"
		},
		buttons: [{
			title: "Cancel"
		},{
			title: "Remove it!",
			func: function(){
				dispatch(removePdf(pdf.id));
			}
		}]
	};
	return (
		<ListGroup.Item className={pdf.is_active ? "list-group-item-primary" : ""}>			
			<Button variant="link"  onClick={e => {
				e.preventDefault();	
				dispatch(changeActivePdf(pdf.index));
			}}><span>{pdf.path}</span> <FontAwesomeIcon icon={faChevronRight} /></Button>	
			{pdf.current_user.id !== null && pdf.user && pdf.current_user.id == pdf.user.id ?
				<ButtonGroup className="mr-2"><UserModal {...signModalRemoveObj} /></ButtonGroup>
				: <></>
			}
			{pdf.user ?  <div><small>(Loaded by {pdf.user.id == pdf.current_user.id ? "you" : pdf.user.name})</small></div> : <></>}
		</ListGroup.Item>
	);
}
PdfView = connect()(PdfView);
export default PdfView;