import React from 'react';
import { connect } from 'react-redux';
import { Route,Link } from 'react-router-dom';

import ListGroup from 'react-bootstrap/ListGroup';
import Button from 'react-bootstrap/Button';
import ButtonGroup from 'react-bootstrap/ButtonGroup';

import { removePage} from '../actions';
import UserModal from './UserModal';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus, faTrashAlt, faEdit } from '@fortawesome/free-solid-svg-icons';

let PageView = ({page, dispatch}) => {	
	let signModalRemoveObj = {
		signIn: false,
		signUp: false,
		sureRemove: true,
		title: "Remove Page",		
		"button": {
			title: "Remove"
		},
		buttons: [{
			title: "Cancel"
		},{
			title: "Remove it!",
			func: function(){
				dispatch(removePage(page.id));
			}
		}]
	};
	return (
		<ListGroup.Item>				
			<Link to={"/pages/" +page.id}><span>{page.name}</span> <FontAwesomeIcon icon={faEdit} /></Link>
			{ page.user && page.user.id == page.current_user.id ? <ButtonGroup className="mr-2"><UserModal {...signModalRemoveObj} /></ButtonGroup> : <></>
					
			}
		{page.user ?  <div><small>(Added by {page.user.id == page.current_user.id ? "you" : page.user.name})</small></div> : <></>}
		</ListGroup.Item>
	);
}
PageView = connect()(PageView);
export default PageView;